<template>
  <div id="container">

  </div>
</template>

<script>
// 
import * as THREE from 'three'
// 
import { OrbitControls } from 'three/examples/jsm/controls/OrbitControls'

export default {
  data () {
    return {
      scene: null, // 
      camera: null, // 
      renderer: null, // 
      sizes: [], // 
      shift: [], // 
    }
  },

  mounted () {
    this.init();
  },

  methods: {
    init () {
      let container = document.getElementById('container');

      // 
      this.scene = new THREE.Scene();
      this.scene.background = new THREE.Color(0x160016); // 

      // 
      this.camera = new THREE.PerspectiveCamera(60,innerWidth / innerHeight,1,1000);
      this.camera.position.set(0,4,21); // 

      // 
      this.renderer = new THREE.WebGLRenderer();
      this.renderer.setSize(innerWidth,innerHeight); // 
      container.appendChild(this.renderer.domElement); // 

      // 
      window.addEventListener("resize",event => {
        this.camera.aspect = innerWidth / innerHeight
        this.camera.updateProjectionMatrix()
        this.renderer.setSize(innerWidth,innerHeight)
      })

      // 
      let controls = new OrbitControls(this.camera,this.renderer.domElement)
      controls.enableDamping = true // 
      controls.enablePan // 
      // uniform
      let gu = {
        time: { value: 0 }
      }

      // 
      // 5ptsyArray.prototype.map()
      let pts = new Array(50000).fill().map(p => {
        // this.sizes
        this.sizes.push(Math.random() * 1.5 + 0.5)
        // pushShift()THREE.Vector
        this.pushShift()
        return new THREE.Vector3().randomDirection().multiplyScalar(Math.random() * 0.5 + 9.5)
        //
      })

      // 
      // 
      // randradius
      for (let i = 0; i < 100000; i++) {
        let r = 10,R = 40;
        let rand = Math.pow(Math.random(),1.5);
        let radius = Math.sqrt(R * R * rand + (1 - rand) * r * r);
        // new THREE.Vector3().setFromCylindricalCoords()
        pts.push(new THREE.Vector3().setFromCylindricalCoords(radius,Math.random() * 2 * Math.PI,(Math.random() - 0.5) * 2));
        this.sizes.push(Math.random() * 1.5 + 0.5);
        this.pushShift()
      }
      // gthis.sizesthis.shift
      let g = new THREE.BufferGeometry().setFromPoints(pts)
      // this.sizesthis.shift
      // FFloat32BufferAttribute
      g.setAttribute("sizes",new THREE.Float32BufferAttribute(this.sizes,1))
      g.setAttribute("shift",new THREE.Float32BufferAttribute(this.shift,4))
      // 
      let m = new THREE.PointsMaterial({
        size: 0.125, // 
        transparent: true, // 
        depthTest: false, // 
        blending: THREE.AdditiveBlending, // 
        // uniform
        // attribute
        onBeforeCompile: shader => {
          shader.uniforms.time = gu.time
          // uniformtime
          // attributethis.sizesthis.shift
          // replace
          shader.vertexShader = `
                uniform float time;
                attribute float sizes;
                attribute vec4 shift;
                varying vec3 vColor;
                ${shader.vertexShader}
                `
            //  ` 
            // replace
            // 
            .replace(
              `gl_PointSize = size;`,
              `gl_PointSize = size * sizes;`
            )
            // 
            .replace(
              `#include <color_vertex>`,
              `#include <color_vertex>
                    float d = length(abs(position)/vec3(40.,10.,40));
                    d=clamp(d,0.,1.);
                    vColor = mix(vec3(227.,155.,0.),vec3(100.,50.,255.),d)/255.;`
            )
            // 
            // 
            .replace(
              `#include <begin_vertex>`,
              `#include <begin_vertex>
                            float t = time;
                            float moveT = mod(shift.x + shift.z * t,PI2);
                            float moveS = mod(shift.y + shift.z * t,PI2);
                            transformed += vec3(cos(moveS) * sin(moveT),cos(moveT),sin(moveS)*sin(moveT)) * shift.w;
                            `
            )
          // 

          // varyingvColor
          // replace
          shader.fragmentShader = `
                    varying vec3 vColor;
                    ${shader.fragmentShader}
                `.replace(
            `#include <clipping_planes_fragment>`,
            `#include <clipping_planes_fragment>
                        float d = length(gl_PointCoord.xy - 0.5);
                    `
          ).replace(
            // 
            `vec4 diffuseColor = vec4( diffuse, opacity );`,
            `vec4 diffuseColor = vec4( vColor, smoothstep(0.5, 0.1, d)/* * 0.5+0.5*/);`
          );
        }
      })
      // -------------------------------------------------------------
      // 
      let p = new THREE.Points(g,m)
      // "ZYX"
      p.rotation.order = "ZYX"
      //  0.2
      p.rotation.z = 0.2
      // pthis.scene
      this.scene.add(p)
      // clock
      let clock = new THREE.Clock()
      // p
      this.renderer.setAnimationLoop(() => {
        // 
        controls.update()
        // clockt0.5
        // 
        let t = clock.getElapsedTime() * 0.5
        // gu.time.value t*Math.PI
        gu.time.value = t * Math.PI
        // pyt*0.05
        p.rotation.y = t * 0.05
        // this.scenethis.camera
        this.renderer.render(this.scene,this.camera)
      })
    },
    // 
    pushShift () {
      this.shift.push(
        Math.random() * Math.PI,
        Math.random() * Math.PI * 2,
        (Math.random() * 0.9 + 0.1) * Math.PI * 0.1,
        Math.random() * 0.9 + 0.1
      )
    }
  },
}
</script>